<template>
    <div class="ind_body">
        <mu-appbar color="#ffd002" :z-depth="1" class="d_nav_fixd">
            <mu-menu slot="left">
                <mu-button flat>
                    <!-- <mu-icon value="menu"></mu-icon> -->
                    <div class="ind_top_icon"><img alt="" src="../assets/images/top_icon.png"></div>
                </mu-button>
                <mu-list slot="content">
                    <side-bar></side-bar>
                </mu-list>
            </mu-menu>
            <mu-tabs :value.sync="active" color="#ffd002" indicator-color="amber800" center>
                <mu-tab class="d_nav_fontsize">精选</mu-tab>
                <mu-tab class="d_nav_fontsize">推荐</mu-tab>
            </mu-tabs>
            <mu-tabs color="#ffd002" center v-show="show">
                <input type="text" name="" placeholder="请输入关键字" class="ind_sousuo_inp">
            </mu-tabs>
            <mu-menu icon slot="right" >
                <mu-button flat >
                    <div class="ind_sousuo_icon"><img alt="" src="../assets/images/sousuo.png"></div>
                </mu-button>
            </mu-menu>
        </mu-appbar>

        <!-- <navbar></navbar> -->
        <cen-left v-if="active === 0"></cen-left>
        <cen-right v-if="active === 1"></cen-right>
    </div>
</template>
<script>
    //import navbar from './nav/navbar'
	import Cen_left from './center/cen_left'
    import Cen_right from './center/cen_right'
    import sidebar from './nav/Sidebar'

    export default {
        data() {
            return {
                active:0
            }
        },
        components: {
            //navbar,
            'cen-left': Cen_left,
            'cen-right': Cen_right,
            'side-bar': sidebar
        }

    };
</script>
<style scoped>
	@import url("../assets/less/main.less");
</style>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
    .mu-flat-button .mu-button-wrapper{
        padding:0;
    }
    .mu-tab-wrapper{
        min-height: 40px;
        padding: 0;
    }
    .mu-appbar .mu-flat-button{
        color:#222222;
    }
</style>



