<template>
    <mu-grid-list class="gridlist-inline-demo" :cols="4" style="background-color:#eeeeee;padding:1% 0;">
        <mu-grid-tile v-for="tile, index in list" :key="index">
            <img :src="tile.image" >
            <span slot="title">{{tile.title}}</span>
        </mu-grid-tile>
    </mu-grid-list>
</template>
    <script>
        import breakfast from '../../assets/images/cen_bar1.jpg';
        import burger from '../../assets/images/cen_bar2.jpg';
        import camera from '../../assets/images/cen_bar3.jpg';
        import hats from '../../assets/images/cen_bar4.jpg';
        import honey from '../../assets/images/cen_bar1.jpg';
        import morning from '../../assets/images/cen_bar2.jpg';
        import vegetables from '../../assets/images/cen_bar3.jpg';
        import waterPlant from '../../assets/images/cen_bar4.jpg';
        export default {
            data () {
                return {
                    list: [{
                        image: breakfast,
                        title: '深度游'
                    }, {
                        image: burger,
                        title: '活动'
                    }, {
                        image: camera,
                        title: '相伴'
                    }, {
                        image: hats,
                        title: '深度游'
                    }, {
                        image: honey,
                        title: '活动'
                    }, {
                        image: morning,
                        title: '相伴'
                    }, {
                        image: vegetables,
                        title: '旅游'
                    }, {
                        image: waterPlant,
                        title: '美景'
                    }]
                }
            }
        }
    </script>
    <style>
        .mu-grid-tile-wrapper{
            padding:6px !important;
            width:33% !important;
        }
        .mu-grid-tile{
            border-radius:8px;
        }
        .gridlist-inline-demo {
            display: flex;
            flex-wrap: nowrap;
            overflow-x: auto;
        }

        .mu-grid-tile-wrapper{
            max-height: 6rem;
        }
        .mu-grid-tile-titlebar{
            position:absolute;
            width:100%;
            top: 0;
            left:0;
            height: 100%;
        }
        .mu-grid-tile-title-container{
            margin-left:0;
        }
        .mu-grid-tile-title {
            font-size: 1.3em;
            text-align: center;
        }
        .mu-grid-tile-subtitle {
            font-size: 2px;
        }
        .mu-icon-button{
            width: 20px;
            height: 20px;
            font-size: 20px;
            padding: 20px;
        }
    </style>



