<template>
    <div>
        <mu-carousel transition="fade">
            <mu-carousel-item>
                <img :src="carouselImg1">
            </mu-carousel-item>
            <mu-carousel-item>
                <img :src="carouselImg2">
            </mu-carousel-item>
            <mu-carousel-item>
                <img :src="carouselImg3">
            </mu-carousel-item>
            <mu-carousel-item>
                <img :src="carouselImg4">
            </mu-carousel-item>
        </mu-carousel>
    </div>
</template>
<script>
    import carouselImg1 from '../../assets/images/cen_bar1.jpg'
    import carouselImg2 from '../../assets/images/cen_bar2.jpg'
    import carouselImg3 from '../../assets/images/cen_bar3.jpg'
    import carouselImg4 from '../../assets/images/cen_bar4.jpg'
    export default {
        data() {
            return {
                carouselImg1,
                carouselImg2,
                carouselImg3,
                carouselImg4
            }
        }
    };
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
    .demo-text {
        padding: 16px;
        background: #fff;
        p {
            margin: 8px 0;
        }
    }
    .mu-carousel-indicator-button{
    	margin: 0 2px !important;
    }
    .mu-carousel-indicator-icon{
    	width: 10px !important;
    	height: 10px !important;
    }
    .mu-carousel {
        height: 160px;
    }
    .mu-carousel-button.mu-icon-button {
        font-size: 40px;
    }
    .mu-carousel-button.mu-icon-button{
    	color: #ffd002;
    }
    .mu-carousel-indicators {
        height: 30px;
    }
    .mu-carousel-indicator-button {
        width: 15px;
        height: 15px;
    }
    .mu-carousel-item > img {
        max-width: 100%;
    }
    .mu-carousel-indicator-button__active .mu-carousel-indicator-icon{
    	background: #ffd002;
    }
</style>


