<template>
    <div>
        <mu-flex class="flex-wrapper d_flex_border" align-items="center" v-for="tile, index in list" :key="index">
            <mu-flex class="flex-demo" fill>
                <div class="right_img">
                    <img alt="" :src="tile.image">
                    <div class="right_img_txt">
                        <h3 slot="title">{{tile.title}}</h3>
                        <p slot="subTitle">{{tile.author}}</p>
                    </div>
                </div>
            </mu-flex>
        </mu-flex>
    </div>
</template>

<script>
    import breakfast from '../../assets/images/cen_bar1.jpg';
    import burger from '../../assets/images/cen_bar2.jpg';
    import camera from '../../assets/images/cen_bar3.jpg';
    import hats from '../../assets/images/cen_bar4.jpg';
    import honey from '../../assets/images/cen_bar1.jpg';
    import morning from '../../assets/images/cen_bar2.jpg';
    import vegetables from '../../assets/images/cen_bar3.jpg';
    import waterPlant from '../../assets/images/cen_bar4.jpg';
    export default {
        data () {
            return {
                list: [{
                    image: breakfast,
                    title: '深度游',
                    author: 'kakali'
                }, {
                    image: burger,
                    title: '活动',
                    author: 'kakali'
                }, {
                    image: camera,
                    title: '相伴',
                    author: 'kakali'
                }, {
                    image: hats,
                    title: '深度游',
                    author: 'kakali'
                }, {
                    image: honey,
                    title: '活动',
                    author: 'kakali'
                }, {
                    image: morning,
                    title: '相伴',
                    author: 'kakali'
                }, {
                    image: vegetables,
                    title: '旅游',
                    author: 'kakali'
                }, {
                    image: waterPlant,
                    title: '美景',
                    author: 'kakali'
                }]
            }
        }
    }
</script>

<style>
    @import url("../../assets/less/main.less");
</style>


