<template>
    <div>
        <mu-appbar color="#ffd002" class="d_nav_fixd">
            <mu-menu slot="left" >
                <mu-button flat @click="return_back">
                    <span class="retu_back_icon"><img src="../../assets/images/return_icon.png" /></span>
                </mu-button>
            </mu-menu>
            <div class="deta_bt_div" color="#ffd002" center>
                <p class="deta_bt" style="text-align:center;">消息</p>
            </div>
        </mu-appbar>

        <div class="news_nr">
            <div class="news_nr_one">
                <router-link to="/News_comment" class="cen_up_a">
                    <div class="news_one_div">
                        <p class="news_one_div_icon">
                            <img src="../../assets/images/news_icon2.png" />
                            <span class="news_one_div_icon_number">2</span>
                        </p>
                        <div class="news_one_div_txt">
                            <p class="n_txt1">评论</p>
                            <p class="n_txt2">这个人很懒，什么都没留下</p>
                        </div>
                        <span class="news_right_icon">
                            <img src="../../assets/images/right_icon.png" />
                        </span>
                    </div>
                </router-link>
                <router-link to="/News_recommend" class="cen_up_a">
                    <div class="news_one_div">
                        <p class="news_one_div_icon">
                            <img src="../../assets/images/tuijian.png" />
                            <span class="news_one_div_icon_number">2</span>
                        </p>
                        <div class="news_one_div_txt">
                            <p class="n_txt1">推荐</p>
                            <p class="n_txt2">美少女的游记</p>
                        </div>
                        <span class="news_right_icon">
                            <img src="../../assets/images/right_icon.png" />
                        </span>
                    </div>
                </router-link>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {

            }
        },
        methods:{
            return_back:function(){
                //alert(123);
                this.$router.go(-1)
            }
        }

    };
</script>

<style>
    @import url("../../assets/less/main.less");
</style>
<style lang="less">
    .mu-flat-button .mu-button-wrapper{
        padding:0;
    }
    .material-icons{
        font-size:2rem;
        color:#222222;
    }
    .deta_bt{
        width: 84%;
    }
</style>


