import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Details from '@/components/details/detailss'
import Landing from '@/components/user/landing'
import Registered from '@/components/user/registered'
import News from '@/components/news/news'
import News_comment from '@/components/news/news_comment'
import News_recommend from '@/components/news/news_recommend'

Vue.use(Router)

export default new Router({
  routes: [
    {
        path: '/',
        name: 'Index',
        component: Index
    },
    {
        path: '/Details',
        name: 'Details',
        component: Details
    },
    {
        path: '/Landing',
        name: 'Landing',
        component: Landing
    },
    {
        path: '/Registered',
        name: 'Registered',
        component: Registered
    },
    {
        path: '/News',
        name: 'News',
        component: News
    },
    {
        path: '/News_comment',
        name: 'News_comment',
        component: News_comment
    },
    {
        path: '/News_recommend',
        name: 'News_recommend',
        component: News_recommend
    }
  ]

})




